import {whoSelected} from "../Utils"
import {useEffect} from "react"
import {isOnline} from "../Utils"
import {memo} from "react"
import moment from "moment"
const ChatBox = ({currentChat,messages,user,isMessageLoading,onlineUsers,updatecurrentChat,statusTyping})=>{
  useEffect(()=>{
    if(messages){
      const messagesBox = document.querySelectorAll(".message-box")

      messagesBox[messagesBox?.length - 1]?.scrollIntoView({ behavior: 'smooth' })
    }
  },[messages])
	return(
		<>
  {/*===============head of somme one wi talk===================================*/}
              <div className="header">
               <i className="ri-arrow-left-line" onClick={()=>updatecurrentChat(null)}></i>
                <div className="img-text">
                  <div className="user-img">
                  <img className="dp" src={whoSelected(user?._id,currentChat?.users)?.picture}  alt="jjd"/>
                </div>
                  <h4>{whoSelected(user?._id,currentChat?.users)?.pseudo}<br/>
                  {(statusTyping?.currentChat === currentChat?._id && 
                  statusTyping?.typing === true && 
                  statusTyping?.currentUser !== user?._id) ?
                   <span>typing...</span>:
                  <span>{isOnline(whoSelected(user?._id,currentChat?.users)?._id,onlineUsers) ? "  Online" : "offline"}</span>
                  }
                  </h4>
                </div>
                <div className="nav-icons">
                  <i className="ri-search-line"></i>
                  <li><i className="fa-solid fa-ellipsis-vertical"></i></li>
                </div>
              </div>

{/*======================message containner=======================================*/}
{
  isMessageLoading && <p style={{textAlign:"center",width:"100%",justifyContent:"center",display:"flex",fontSize:"2rem",height:"100%"}}></p>
}
{
  !isMessageLoading &&
<div className="chat-container">
  {
    messages && messages?.map((message,index)=><div key={index} className={message?.sender?._id === user?._id ? "message-box my-message":"message-box friend-message"}><p>{message?.message}<br/>
      <span>{moment(message.createdAt).calendar()}{message?.sender?._id === user?._id && <span className="marksdeliverpending">{message.status === "PENDING"?<i className="ri-loader-2-fill"></i>:<i className="ri-check-double-line"></i>}</span>}</span></p></div>)
  }
</div>
}

	</>
		)
}

export default memo(ChatBox)