import{useFetchRecipientUser} from "../../hooks/userFetchRecipient"
import{useFetchLatestMessage} from "../../hooks/useFetchLatestMessage"
import {memo} from "react"
import{unreadNotificationFunc} from "../utils/Services"
import axios from "axios"
import {SERVER_URL} from "../../Domain"
import moment from "moment"
import {whoSelected} from "../Utils"
import {useEffect,useState,useCallback} from "react"
const UserChat = ({user,chat,notifications,markThisNotificationAsRead,currentChat,lastetMessageSend,statusTyping})=>{
	const {recipientUser}  = useFetchRecipientUser(user,chat?.users)
  const unreadNotifications = unreadNotificationFunc(notifications)
  const thisUserNotification  = unreadNotifications?.filter(
    n => n.senderId === whoSelected(user?._id,chat?.users)?._id
    )
const [latestMessage,setLatestMessage] = useState(null)

useEffect(()=>{
  if(lastetMessageSend === undefined) return
    if(chat._id !== lastetMessageSend.chat._id) return
      if(currentChat?._id !== lastetMessageSend?.chat._id) return
  setLatestMessage(lastetMessageSend)
},[lastetMessageSend])

    const getMessages =useCallback(async()=>{
            await axios({
            method: "get",
            url: `${SERVER_URL}/api/message/${chat?._id}`,
            withCredentials: true,
            })
            .then(({data}) => {
            const lastMessage = data[data?.length - 1]
            setLatestMessage(lastMessage)
            })
            .catch((error) => {
           console.log(error)
            })
    },[notifications])
useEffect(()=>{
  getMessages()
},[getMessages])
 



    const truncutor=(message)=>{
      if(message?.length > 20){
        return `${message?.substr(0,10)}.....`
      }
      return message
    }
	return(<div className={chat?._id === currentChat?._id ?"chat-box active": "chat-box"} onClick={()=>{
    if(thisUserNotification?.length !== 0){
      markThisNotificationAsRead(thisUserNotification,notifications)
    }
  }}>
          <div className="img-box">
            <img className="img-cover" src={whoSelected(user?._id,chat?.users)?.picture} alt={Math.random()}/>
          </div>
          <div className="chat-details">
            <div className="text-head">
            {
              !chat?.isGroupChat && <h4>{whoSelected(user?._id,chat?.users)?.pseudo}</h4>
            }
             {chat?.isGroupChat && <h4>{chat?.chatName}</h4>} 
             <p className="time unread">{moment(latestMessage?.createdAt).calendar()}</p>
            </div>
            {
              statusTyping && statusTyping.currentChat === chat?._id && statusTyping.typing === true ?
              <div className="text-message">typing...</div>:
               <div className="text-message">
              <p>{truncutor(latestMessage?.message)}</p>
              {latestMessage?.sender?._id === user?._id && <span>{latestMessage.status === "PENDING"?<i className="ri-loader-2-fill"></i>:<i className="ri-check-double-line"></i>}</span>}
              {
                thisUserNotification?.length > 0 && <b>{thisUserNotification?.length > 0 ? thisUserNotification.length : "" }</b>
              }
             
            </div>
            }          


            
          </div>
        </div>)
}
export default memo(UserChat);
// export const memorizedUserChat = React.memo(UserChat)